import bookshelf from '../db';
import Country from './country';
const tblName = 'city';

class City extends bookshelf.Model {
  get tableName() {
    return tblName;
  }

  country() {
    return this.hasOne(Country);
  }

  static fetchById(id) {
    return City.where('id', id).fetch();
  }
}

export default City;
